import Vue from 'vue'
import Router from 'vue-router'
import AppView from '@/components/app-view'
const page = name => () => import('@/pages/' + name)
Vue.use(Router)

export default new Router({
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  },
  routes: [
    {
      path: '/',
      name: 'app_view',
      redirect: '/view',
      component: AppView,
      children: [
        {
          path: '/view',
          name: 'view',
          component: page('view'),
          meta: {title: ''}
        },
        {
          path: '/gantt',
          name: 'gantt',
          component: page('gantt'),
          meta: {title: '甘特图'}
        },
        {
          path: '/dragForm',
          name: 'dragForm',
          component: page('dragForm'),
          meta: {title: '拖拽表单'}
        },
        {
          path: '/calendar',
          name: 'calendar',
          component: page('calendar'),
          meta: {title: '日历'}
        },
        {
          path: '/h5Editor',
          name: 'h5Editor',
          component: page('h5Editor'),
          meta: {title: 'h5编辑器'}
        },
        {
          path: '/driver',
          name: 'driver',
          component: page('driver'),
          meta: {title: '向导'}
        }
      ]
    },
    {
      path: '/charts',
      name: 'charts',
      redirect: '/mapCharts',
      component: AppView,
      meta: {title: '图表'},
      children: [
        {
          path: '/mapCharts',
          name: 'mapCharts',
          component: page('charts/mapCharts'),
          meta: {title: '地图'}
        },
        {
          path: '/radarCharts',
          name: 'radarCharts',
          component: page('charts/radarCharts'),
          meta: {title: '雷达图'}
        },
        {
          path: '/barCharts',
          name: 'barCharts',
          component: page('charts/barCharts'),
          meta: {title: '柱状图'}
        },
        {
          path: '/pieCharts',
          name: 'pieCharts',
          component: page('charts/pieCharts'),
          meta: {title: '饼图'}
        },
      ]
    },
  ]
})



